import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TePoGrnComponent } from './te-po-grn.component';

const routes: Routes = [
    {
        path: '', component: TePoGrnComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class TePoGrnRoutingModule { }



