import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { TePoGrnRoutingModule } from './te-po-grn-routing.module';
import { TePoGrnComponent } from './te-po-grn.component';
import { TePoGrnAddComponent } from './te-po-grn-add/te-po-grn-add.component';
import { TePoGrnEditComponent } from './te-po-grn-edit/te-po-grn-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    TePoGrnRoutingModule
  ],
  entryComponents: [
    TePoGrnAddComponent,
    TePoGrnEditComponent
  ],
  declarations: [
    TePoGrnComponent,
    TePoGrnAddComponent,
    TePoGrnEditComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class TePoGrnModule { }



