  saveBtn(post) {
    post['store_id'] = this.myComboBox.val();
    post['vendor_id'] = this.myComboBox2.val();
    post['grnPOItems'] = this.grnPoAddGrid.getrows();
    let account_head = this.accHeadAddGrid.getrows();
    post['accountTransaction'] = {};
    post['accountTransaction']['accountTransactionSplits'] = account_head;
    post['type'] = 'dgrn';
    this.myValidator.validate(document.getElementById('grnPoForm'));
    // if (post['store_id'] && post['vendor_id'] && post['grnPOItems'].length > 0) {
      this.jqxLoader.open();
      let accHeadFlag: boolean = false;
      for (let i = 0; i < account_head.length; i++) {

      }
      this.gps.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('errorMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.errorNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('errorMessage');
          messageDiv.innerText = data['message'];
          this.errorNotification.open();
          this.jqxLoader.close();
        }
      );
    // }
  }
  
